import React, { useEffect, useState } from 'react';
import { GifAppData } from './GifAppData';

export const GifApp = ({ categorias }) => {
	const [images, setImages] = useState([]);
	useEffect(() => {
		gifApi();
	}, []);
	const gifApi = async () => {
		const link = `https://api.giphy.com/v1/gifs/search?q=${encodeURI(
			categorias
		)}&limit=5&api_key=XTsBFEF3BIG7aXxDmyoa37NmzLOk8ncO`;
		const resp = await fetch(link);
		const { data } = await resp.json();

		const gif = data.map((gif) => {
			return {
				id: gif.id,
				title: gif.title,
				url: gif.images.downsized_medium.url,
			};
		});
		setImages(gif);
	};

	return (
		<>
			<h4>{categorias}</h4>
			<div className="d-flex flex-wrap">
				{images.map((img) => (
					<GifAppData key={img.id} {...img} />
				))}
			</div>
		</>
	);
};
